export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      winHeight: 0,
      winWidth: 0
    };
  }

  componentDidMount() {
    const winWidth = document.body.clientWidth || document.documentElement.clientWidth;
    const winHeight = document.body.clientHeight || document.documentElement.clientHeight;

    this.setState({
      winHeight,
      winWidth
    });
  }

  render() {
    const { winWidth, winHeight } = this.state;

    return (
      <div style={{ width: `${winWidth}px`, height: `${winHeight}px` }}>
        <Tree
          orientation="vertical"
          translate={{ x: winWidth / 2, y: 40 }}
          nodeSize={{ x: 20, y: 30 }}
          zoomable={true}
          zoom={1}
          data={myTreeData} />
      </div>
    );
  }
}
